body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.my-main
{
    background-image:linear-gradient(rgba(0,0,0,0.3),rgba(0,0,0,0.3)),url("https://images.pexels.com/photos/4551832/pexels-photo-4551832.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
    background-repeat: no-repeat;
    background-position: center;
    background-size: hover;
    height:400px;
    display:flex;
    justify-content: center;
    align-items:center;
    flex-direction: column;
    
}

.my-main h1,h2
{
    color: antiquewhite;
 
}


.button-input
{   
    width: 40%;
    margin-top: 20px;
    
}
@font-face {
    font-family: "main";   /*Can be any text*/
    src: url("/recipewebsite/src/fonts/Poppins/Poppins-Black.ttf");
  }

@font-face {
    font-family: "second";   /*Can be any text*/
    src: url("/recipewebsite/src/fonts/CantataOne-Regular.ttf") ;
}

@font-face {
font-family: "third";   /*Can be any text*/
src: url("/recipewebsite/src/fonts/Montserrat-Bold.ttf");
}

.meals-component
{   
    
    display:flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    padding-top: 20px;
    font-family: "second";
    
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

